* {
  font-family: Arial, Helvetica, sans-serif;
}

.square {
  height: 100%;
  width: 1.25%;
  float: left;
}

.row {
  float:left;
  width: 100%;
  height: 2.5%;
}

.buttonSquare {
  height: 100%;
  width: 1.25%;
  float: left;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}

.content-wrapper {
  width: 768px;
}

.grid {
  width: 100%;
  height: 384px;
  display: inline-block;
}

@media only screen and (max-width: 768px) {
  .content-wrapper {width: 100%;}
  .button-wrapper {width: 100%;}
}


.nextButton, .backButton {
  background-color: #23d194;
  border: 0;
  padding: 10px;
  margin: 1px;
}

.next10Button, .back10Button {
  background-color: #23d194;
  border: 0;
  padding: 10px;
  margin: 1px;
}

.jumpToStartButton, .jumpToEndButton {
  background-color: #23d194;
  border: 0;
  padding: 10px;
  margin: 1px;
}

.controlButton {
  box-sizing: border-box;
  height: 50px;
  background-color: #38e1ff;
  border: 0;
  padding: 17px;
  margin: 1px;
  float: left;
  outline: none;
}
.controlButton:hover {
  background-color: #ff5638;
  color: white;
}

.test {
  background-color: inherit;
  border: inherit;
  padding: inherit;
  margin: inherit;
}

.formContainer {
  box-sizing: border-box;
  height: 50px;
  background-color: #ff5638;
  float: left;
  padding: 10px;
  margin: 1px;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.infoContainer {
  padding: 15px;
}
